import { randomColor } from "randomcolor";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import axios from 'axios';

var json = '[{"time":{"start":"2021-03-24T12:00:00+04:30", "recurse":"weekly", "duration":120}, "text":"study"}, {"time":{"start":"2021-03-26T20:00:00+04:30", "recurse":"onetime", "duration":15}, "text":"train something"}]';
var days = ["yeshanbe", "doshanbe", "seshanbe", "charshanbe", "panshanbe", "jome", "shanbe"];
var selectedDay = new Date();

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginTop: theme.spacing(1),
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 300,
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  Task: {
    marginTop: "10px",
    marginBottom: "10px",
    marginLeft: "16px",
    marginRight: "16px"
  },
  TaskContent: {
    paddingTop: "24px",
    paddingBottom: "24px",
    display: "grid",
    gridTemplateColumns: "20fr 1fr",
    gridTemplateAreas: '"task trash"'
  },
  TaskText: {
    gridArea: "task",
    cursor: "default",
    
  },
  DeleteIcon: { gridArea: "trash", color: "#808080", cursor: "pointer" },
}));

function Showtask(task){
  var kk = task.value;
  const classes = useStyles();
  const mobile = 700;

  const [bg,setBg]=useState(randomColor());
  return(
      <Card
        className={classes.Task}
        style={{ backgroundColor: bg }}
      >
        <CardContent className={classes.TaskContent}>
          <MediaQuery minDeviceWidth={mobile}>
            <Typography
              variant="body1"
              color="textSecondary"
              component="p"
              className={classes.TaskText}
            >
              {kk.text}
              {" for "+kk.time.duration+" minutes"}
            </Typography>
          </MediaQuery>
          <MediaQuery maxDeviceWidth={mobile - 1}>
            <Typography
              variant="body1"
              color="textSecondary"
              component="p"
              className={classes.TaskText}
            >
              {kk.text}
              {" for "+kk.time.duration+" minutes"}  
            </Typography>
          </MediaQuery>
          <DeleteOutlineIcon
            className={classes.DeleteIcon}
          />
        </CardContent>
      </Card>
    
  );
}

function Showselectedtasks(tasks){
  return( 
  <div>
      {tasks.value.map((value, index) => {
        return <Showtask value={value}/>
      })}    </div>
  );
}

function Filtertasks(props){
  var tasks = props.value;
  var selectedTasks = [];

    var i;
    for (i =0 ; i < tasks.length ; i++ ){
      var date = new Date(tasks[i].time.start);
      if ((date.getFullYear() === selectedDay.getFullYear() && date.getMonth() === selectedDay.getMonth() && date.getDate() === selectedDay.getDate()) || (tasks[i].time.recurse === "weekly" && date.getDay() === selectedDay.getDay())){
        selectedTasks.push(tasks[i]);
      }}
    return(
      <div>
        <Showselectedtasks value={selectedTasks}/>
      </div>
    );
}

function SimpleDialog(props) {
  const { onAdd, onClose, open } = props;
  const classes = useStyles();
  const [values, setValues] = useState({});

  const handleClose = () => {
    onClose();
  }
const handleAdd = (event) => {
  event.preventDefault();
  let kk ={time:{start:values.start, recurse:values.recurse, duration:values.end},text:values.text};
  onAdd(kk);
  onClose();
}

const handleChange = (event) => {
  event.persist();
  setValues(values => ({ ...values, [event.target.name]: event.target.value }));
};

  return (
    <Dialog onClose={handleClose} aria-labelledby="form-dialog-title" open={open} >
      <DialogTitle id="form-dialog-title">Create a new Timeblock</DialogTitle>
      <TextField
        name="start"
        value={values.start}
        onChange={handleChange} 
        required
        id="datetime-local"
        label="Choose start date and time"
        type="datetime-local"
        defaultValue={selectedDay.toString}
        className={classes.textField}
        InputLabelProps={{
          shrink: true,
        }}
      />
        <TextField
        name="end"
        value={values.end}
        onChange={handleChange} 
        required
        id="time"
        label="End time"
        type="time"
        className={classes.textField}
        InputLabelProps={{
          shrink: true,
        }}
        inputProps={{
          step: 300, // 5 min
        }}
      />
      <TextField required id="standard-required" label="Task" className={classes.textField} name="text"         value={values.text}
        onChange={handleChange} />
      <FormControl className={classes.formControl} required>
        <InputLabel id="demo-simple-select-label">Recurse</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          name="recurse"
          value={values.recurse}
          onChange={handleChange} 
        >
          <MenuItem value={"weekly"}>Weekly</MenuItem>
          <MenuItem value={"daily"}>Daily</MenuItem>
          <MenuItem value={"onetime"}>One time</MenuItem>
        </Select>
      </FormControl>
      
      <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button  color="primary" onClick={handleAdd}>
            Add
          </Button>
      </DialogActions>
    </Dialog>
  );}

  async function postData(url = '', data = {}) {
    // Default options are marked with *
    const response = await fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    mode: 'cors', // no-cors, *cors, same-origin
    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    credentials: 'same-origin', // include, *same-origin, omit
  
    redirect: 'follow', // manual, *follow, error
    referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
    body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
    return response.json(); // parses JSON response into native JavaScript objects
    }
    
    
function App() {
  const [open, setOpen] = React.useState(false);
  const [jj, setJj] = useState([]);
  useEffect(() => {
    const fetchData = async () => {
      const result = await axios(
        'https://cloud.pdcommunity.ir/timeblox/showjson.php',
      );
      setJj(result.data);
    }
    fetchData();
  }, []);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleAdd = (obj) =>{

    //var url = 'https://cloud.pdcommunity.ir/timeblox/setjson.php?'+ JSON.stringify(obj);
    postData('https://cloud.pdcommunity.ir/timeblox/setjson.php', obj)
    .then(data => {
    setJj([...jj,obj]);
    console.log(data); // JSON data parsed by data.json() call
    });
  }
  return (
    
    <div className="App">
      <header className="App-header">
      <div id='kk'>{selectedDay.toLocaleString('fa-IR')
.toString()}
        <IconButton aria-label="delete" color="primary" onClick={handleClickOpen}>
          <AddCircleIcon />
        </IconButton>
        <SimpleDialog onAdd={handleAdd} open={open} onClose={handleClose} />
        </div>
      <div>{days[selectedDay.getDay()]}</div>
      <Filtertasks value={jj}/>

      </header>
    </div>
  );
}

export default App;
